import React from 'react'
import s from './Settings.module.css'
import { Box, FormControlLabel, InputAdornment, Radio } from '@material-ui/core'
import MyTextField from '../../../../../common/MyTextField'
import MyCheckbox from '../../../../../common/MyCheckbox'
import MyRadioGroup from '../../../../../common/MyRadioGroup'
import storage from 'store2'

function Settings () {
  const fieldWidth = 250

  return (
    <>
      <div className={s.times}>
        <Box width={fieldWidth}>
          <MyTextField
            fullWidth
            name="sendInterval"
            label=" "
            InputProps={{
              startAdornment: <InputAdornment position="start">:</InputAdornment>
            }}
            inputProps={{ min: '0', step: 'any' }}
            variant="filled"
            type="number"
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              const fields = storage.local.get('fields')
              storage.local.set('fields', {
                ...fields,
                sendInterval: e.currentTarget.value
              })
            }}
          />
        </Box>
        <Box width={fieldWidth}>
          <MyTextField
            fullWidth
            name="autoPauseTimeout"
            label="  "
            InputProps={{
              startAdornment: <InputAdornment position="start">:</InputAdornment>
            }}
            inputProps={{ min: '0', step: 'any' }}
            variant="filled"
            type="number"
            placeholder=""
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              const fields = storage.local.get('fields')
              storage.local.set('fields', {
                ...fields,
                autoPauseTimeout: e.currentTarget.value
              })
            }}
          />
        </Box>
      </div>

      <div className={s.checkboxes}>
        <FormControlLabel control={
          <MyCheckbox
            size="small"
            name="onePass"
            color="secondary"
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              const fields = storage.local.get('fields')
              storage.local.set('fields', {
                ...fields,
                onePass: e.currentTarget.checked
              })
            }}
          />
        } label=" "/>

        <MyRadioGroup
          row
          aria-label="captchaMode"
          name="captchaMode"
          onChange={(e) => {
            const fields = storage.local.get('fields')
            storage.local.set('fields', {
              ...fields,
              captchaMode: e.currentTarget.value
            })
          }}>
          <FormControlLabel control={
            <Radio
              size="small"
              color="secondary"
              value=""
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                const fields = storage.local.get('fields')
                storage.local.set('fields', {
                  ...fields,
                  antiCaptcha: e.currentTarget.value
                })
              }}
            />
          } label=""/>
          <FormControlLabel control={
            <Radio
              size="small"
              color="secondary"
              value=" "
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                const fields = storage.local.get('fields')
                storage.local.set('fields', {
                  ...fields,
                  antiCaptcha: e.currentTarget.value
                })
              }}
            />
          } label=" "/>
          <FormControlLabel control={
            <Radio
              size="small"
              color="secondary"
              value=" "
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                const fields = storage.local.get('fields')
                storage.local.set('fields', {
                  ...fields,
                  antiCaptcha: e.currentTarget.value
                })
              }}
            />
          } label=" "/>
        </MyRadioGroup>
      </div>
    </>
  )
}

export default Settings
