import { addLogItem } from '../../../ducks/spamer/action-creators'
import { ThunkType } from '../../../store'
import { SpamValuesType } from '../../../../types/types'

//          true,  false
const validate = (
  spamValues: SpamValuesType,
  setFieldError: (field: string, message: string) => void
): ThunkType<boolean> => {
  return async (dispatch, getState) => {
    const accounts = getState().accountsReducer.accounts
    let isSuccess = true

    if (!accounts.length) {
      dispatch(addLogItem('   ', 'error', `    - ${Date.now()}`))
      isSuccess = false
    } else if (!accounts.some(account => account.isEnabled)) {
      dispatch(addLogItem('  ', 'error', `   - ${Date.now()}`))
      isSuccess = false
    }

    if (!spamValues.addresses.length) {
      setFieldError('addresses', '  ')
      isSuccess = false
    }

    if (!spamValues.message && !spamValues.attachment) {
      setFieldError('message', '     ')
      setFieldError('attachment', '     ')
      isSuccess = false
    }

    if (spamValues.captchaMode === '' && !spamValues.antiCaptchaKey) {
      setFieldError('antiCaptchaKey', '  ')
      isSuccess = false
    }

    return isSuccess
  }
}

export default validate
